import React from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { viewAdvertisement } from "../apiServices/home/homeHttpService";

function AdvertisementDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();

  const { data: details } = useQuery({
    queryKey: ["AdDetail", id],
    queryFn: async () => {
      return viewAdvertisement(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.advertisement,
  });

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <Link
            to={`/admin/advertisement-package?tab=${
              queryParams.get("tab") || 1
            }&page=${queryParams.get("page") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div>
                  <h2 className="fs-5 fw-semibold">
                    Advertisement Package Plan
                  </h2>
                </div>
                <div className=" mt-4 w-100">
                  <div className="d-flex w-100">
                    <div className="w-50 py-4 px-4 rounded-3 border me-4">
                      <div className="d-flex justify-content-center align-items-center">
                        <div className="text-center">
                          <h2 className="fs-4 fw-semibold">
                            {details?.planName}
                          </h2>
                          <p className="mt-4 mb-4">{details?.description}</p>
                          <button className="comman_btn w-fit mx-auto">
                            ${details?.price} per month
                          </button>
                        </div>
                      </div>
                    </div>
                    <div className="w-50 py-4 px-5 bg-main rounded-3">
                      <h2 className="text-white ">Benifits</h2>
                      <ul className="subscription-plan-view-ul text-white p-0">
                        {details?.features?.map((item, index) => (
                          <li key={index}>{item}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AdvertisementDetails;
